.container{
    @include min-screen(1200px){
        min-width: 1240px;
    }
}

.footer-button{
  padding-top: 40px;
  text-align: center;
}

.section-highlight{
  margin-bottom: 20px;
}

.section-most-popular{
  padding: 20px;

  header{
    border-bottom: 1px solid $white;
    padding-bottom: 12px;
    margin-bottom: 20px;

    .title{
      .icon{
        margin-right: 20px;
      }
    }
  }

  .listing-card ._card + ._card{
    margin-top: 10px;
  }
}

.section-news{
  .listing-card{
    ._card{
      padding-bottom: 10px;
      margin-bottom: 10px;
    }
  }
}

._section{
  padding: 40px 0px;

  @include max-screen(767px){
    padding: 20px 0px;
  }

  &-title{
    position: relative;
    width: 100%;
    white-space: nowrap;
    color: $white;
    text-align: center;
    font-weight: 200;
    margin-bottom: 40px;
    overflow: hidden;

    @include max-screen(767px){
      font-weight: 100;
      @include font-size(40);
      margin-bottom: 15px;
    }

    &:before,
    &:after{
      background-color: $white;
      content: "";
      display: inline-block;
      height: 1px;
      position: relative;
      vertical-align: middle;
      width: 45.5%;
    }

    &:before{
      right: 20px;
      margin-left: -50%;
    }

    &:after{
      left: 20px;
      margin-right: -50%;
    }

    .name{
      padding: 0px 10px;
      display: inline-block;
    }
  }
}

.form-subscribe{
  max-width: 400px;
  margin: 0 auto;
  overflow: hidden;

  input[type="email"]{
    border-bottom: 0px;
    text-align: center;
    width: 100%;
  }

  .form-group{
    padding-bottom: 10px;
    border-bottom: 1px solid #ccbaa8;
  }

  .group-button{
    margin-top: 30px;
  }
}
