html{
	min-width: 320px;
	font-size: 62.5%;
	touch-action: manipulation;

	-webkit-overflow-scrolling : touch;
	-webkit-tap-highlight-color : transparent;
	-webkit-text-size-adjust : 100%;
	-ms-text-size-adjust : 100%;
	overflow-x: hidden !important;
}

body{
	margin : 0;
	padding : 0;
	width : 100%;
	color: $basecolor;
	font-family: $basefont;
	font-weight: normal;
	@include font-size($basefontsize);
	line-height: 1;

	//font thin for firefox
	-moz-osx-font-smoothing: grayscale;
	-webkit-font-smoothing: antialiased;
}

// sticky footer
// -------------------------
html {
	position: relative;
	min-height: 100%;
}
body {

}

#main{

}

#footer {
  background-color: #0e0e0e;
  padding-top: 65px;
  padding-bottom: 50px;

  .copy-right{
    font-family: $brandfont;
    font-weight: 200;
    @include font-size(20);
  }

  .list-menu{
    a{
      font-family: $brandfont;
      font-weight: 300;
      @include font-size(24);
      color: $white;
      padding: 0px 15px;
      @include transition(all $transition);

      &:hover{
        color: $brand-color;
      }
    }
  }

  .list-social{
    padding: 40px 0px;

    a{
      color: $white;
      padding: 0px 10px;
      font-size: 24px;
    }
  }
}


//bug position:fixed scroll gutter in chrome
html.chrome.ios.mobile,
html.chrome.ios.mobile body {
	height:100%;
}

// disable :hover when scrolling on mobile
.disable-hover,
.disable-hover * {
  pointer-events: none !important;
}

// Disable hover effects pause
html.touch .disable-focus:hover {
    pointer-events: none !important;
}


// Disable background yellow
input:-webkit-autofill{
	  &,
	  &:hover,
	  &:focus,
	  &:active{
	    background-color: #FFFFFF !important;
	    color: #555 !important;
	    -webkit-box-shadow: 0 0 0 1000px white inset !important;
	    -webkit-text-fill-color: #555555 !important;
	}
}

label > * {
    display: block;
    // pointer-events: none;
}

