.button,
a.button{
  min-height: 50px;
  color: $white;
  font-family: $brandfont;
  font-weight: 200;
  @include font-size(30);
  @include transition(all $transition);
  display: inline-block;

  &._size-m{
    min-height: 62px;
  }

  &-outline{
    border: 1px solid $white;
    padding: 9px 30px;
    @include bg-transparent();

    .icon{
      margin-left: 20px;
    }

    &:hover{
      background-color: $brand-color;
      cursor: pointer;
      border: 1px solid $brand-color;
    }
  }
}