.list-author-by{
    font-family: $brandfont;
    font-weight: 100;
    @include font-size(24);
    color: $white;

    li + li{
      border-left: 1px solid $white;
      padding-left: 10px;
    }

    .name,
    .day{
        color: $brand-color;
    }
}


.listing-card{
  ._card{
    & + ._card{
      margin-top: 20px;
    }
  }
}


._card{
	position: relative;

	.thumb{
		position: relative;
		overflow: hidden;
    box-sizing: border-box;
    background-color: #eeeeee;

    .sign-number{
      position: absolute;
      z-index: 10;
      width: 50px;
      height: 50px;
      line-height: 50px;
      background-color: #0e0e0e;
      left: 0;
      top: 0;
      font-family: $brandfont;
      font-weight: 400;
      @include font-size(60);
      color: #f7b787;
      font-style: normal;
      text-align: center;
    }


		&.zoom{
			padding-bottom: 57%;  //default card artical
			width: 100%;
    }

    .parent{
        display: block;
        width: 100%;
    }

    .img-photo{
        display: block;
        background-size: cover;
        background-position: center center;
        background-repeat: no-repeat;
        position: relative;

        width: 100%;
        margin: 0 auto;

    }
  }

	.cat{
		color: $white;
    font-family: $brandfont;
    font-weight: 100;
    @include font-size(24);

    @include max-screen(767px){
      @include font-size(12);
    }
	}

	.title{
    margin-bottom: 10px;

    a{
      color: $white;

      &:hover{
        color: $brand-color;
      }
    }

    @include max-screen(991px){
      @include font-size(26);
    }

    @include max-screen(767px){
      @include font-size(18);
    }
  }

  &._border-bottom{
    border-bottom: 1px solid #707070;
  }


  &._card-row{
    .info{
      padding: 10px 0px;
    }

    .title{
      @include min-screen(992px){
        @include font-size(30);
      }
    }
  }

  &._card-column{
      .thumb{
          max-width: 498px;

          .section-highlight &{
            max-width: 100%;
          }
      }

      &._size-big{
        .info{
          padding-top: 20px;
          padding-right: 20px;

          @include min-screen(1200px){
            padding-top: 40px;
            padding-right: 40px;
          }

          @include max-screen(991px){
            padding-top: 10px;
            padding-right: 10px;
            padding-bottom: 10px;
          }
        }
      }

      .info{
        .section-highlight &{
          padding-left: 40px;

          @include max-screen(991px){
            padding-left: 10px;
          }
        }
      }
  }

  &._card-popular{
    .thumb{
      margin-bottom: 5px;
    }
    .cat{
      @include font-size(18);
    }
    .title{
      @include font-size(24);
      font-weight: 300;
      margin-bottom: 0;
    }
  }

  &._card-content{
    background-color: #1e1e1e;
    display: inline-block;
    padding: 20px;
    @include transition(all $transition);
    width: 100%;

    a{
      &:hover{
        color: $white;
      }
    }

    &:hover{
      background-color: $brand-color;

      .img-photo{
        &:before{
          content: "";
          position: absolute;
          width: 100%;
          height: 100%;
          z-index: 3;
          background-color: rgba(#d88f5b, 0.23);
        }

        &:after{
          content: "";
          position: absolute;
          width: 100%;
          height: 100%;
          z-index: 2;
          @include linear-gradient(180deg,
          #000000 0%,
          #ffffff 100%);
          opacity: 0.2;
        }
      }
    }

    @include max-screen(767px){
      padding: 15px;
    }

    .thumb{
      margin-bottom: 20px;

      .icon{
        position: absolute;
        z-index: 20;
        left: 50%;
        top: 50%;
        margin-top: -39px;
        margin-left: -55px;
      }
    }

    .title{
      @include font-size(30);
    }
  }
}
